import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import './index.css'; // Tailwind CSS
import Hls from "hls.js";

function App() {
const [page, setPage] = useState("login"); // 'login' or 'register'
const [username, setUsername] = useState("");
const [password, setPassword] = useState("");
const [token, setToken] = useState(null);
const [userRole, setUserRole] = useState(null);
const [url, setUrl] = useState("");
const [message, setMessage] = useState("");
const [cookies, setCookies] = useState("");
const [videoSrc, setVideoSrc] = useState("");
const [loading, setLoading] = useState(false);
const [streamUrl, setStreamUrl] = useState("");
const videoRef = useRef(null);
const [ocrText, setOcrText] = useState("");
const [file, setFile] = useState("");

useEffect(() => {
  if (!streamUrl) return;

  const video = videoRef.current;

  if (Hls.isSupported()) {
    const hls = new Hls();
    hls.loadSource(streamUrl);
    hls.attachMedia(video);
  } else if (video.canPlayType("application/vnd.apple.mpegurl")) {
    video.src = streamUrl;
  }
}, [streamUrl]);

const register = async () => {
try {
const res = await axios.post("https://my-flask-project-youtube-dl.onrender.com/api/register", {
username,
password,
role: "user",
});
setMessage(res.data.message);
setPage("login");
} catch (err) {
setMessage(err.response?.data?.error || "Register failed");
}
};

const login = async () => {
try {
const res = await axios.post("https://my-flask-project-youtube-dl.onrender.com/api/login", {
username,
password,
});
setToken(res.data.access_token);
console.log(res.data.access_token);
const payload = JSON.parse(atob(res.data.access_token.split(".")[1]));
setUserRole(payload.sub.role);
setMessage("Login successful");
} catch (err) {
setMessage(err.response?.data?.error || "Login failed");
}
};

const logout = () => {
setToken(null);
setUserRole(null);
setUrl("");
setMessage("");
};

const uploadCookies = async () => {
if (!token || userRole !== "admin") {
alert("Admin access required");
return;
}
  try {
    await axios.post(
      "https://my-flask-project-youtube-dl.onrender.com/api/upload-cookies",
      { cookies },
	{ headers: { Authorization: `Bearer ${token}` } }
    );
    alert("Cookies uploaded successfully");
  } catch (err) {
    setMessage(err.response?.data?.error || "Failed to upload cookies");
    alert("Failed to upload cookies");
  }
};

const downloadFile = async (filename) => {
  const response = await fetch(
    `https://my-flask-project-youtube-dl.onrender.com/api/file/${filename}`,
    {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    }
  );

  if (!response.ok) {
    alert("Download failed");
    return;
  }

  const blob = await response.blob();
  const url = window.URL.createObjectURL(blob);

  const a = document.createElement("a");
  a.href = url;
  a.download = filename;
  document.body.appendChild(a);
  a.click();
  a.remove();

  window.URL.revokeObjectURL(url);
  setMessage("Download successfully");
};

const playVideo = async (filename) => {
  const res = await fetch(
    `https://my-flask-project-youtube-dl.onrender.com/api/file/${filename}`,
    {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    }
  );

  if (!res.ok) {
    alert("Failed to load video");
    return;
  }

  const blob = await res.blob();
  const videoUrl = URL.createObjectURL(blob);
  setVideoSrc(videoUrl);
};

const getStreamUrl = async () => {
  /*const res = await fetch(
    "https://my-flask-project-youtube-dl.onrender.com/api/stream-url",
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({
        url: url,
      }),
    }
  );

  const data = await res.json();*/
  //return data.stream_url;
  //const blob = await data.stream_url.blob();
  const res = await axios.post(
    "https://my-flask-project-youtube-dl.onrender.com/api/stream-url",
	{ url },
	{ headers: { Authorization: `Bearer ${token}` } }
  );
  //const blob = await res.data.stream_url.blob();
  //const videoUrl = URL.createObjectURL(blob);
  //setVideoSrc(res.data.stream_url);
  /*const streamRes = await fetch(res.data.stream_url);
  const blob = await streamRes.blob();

  const videoUrl = URL.createObjectURL(blob);
  setVideoSrc(videoUrl);*/
  //setVideoSrc(res.data.stream_url);
  setStreamUrl(res.data.stream_url);
};

const uploadOCR = async () => {
if (!token || userRole !== "admin") {
alert("Admin access required");
return;
}
  const formData = new FormData();
  formData.append("file", file);

try {
  setLoading(true);
  const res = await axios.post(
    "https://my-flask-project-youtube-dl.onrender.com/api/ocr",
    formData,
    {
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "multipart/form-data",
      },
    }
  );

  setOcrText(res.data.text);
  setLoading(false);
  } catch (err) {
    setMessage(err.response?.data?.error || "Failed to upload OCR");
    alert("Failed to upload OCR");
    setLoading(false);
  }

};

const handleDownload = async () => {
if (!token || userRole !== "admin") {
alert("Admin access required");
return;
}
setLoading(true);
try {
const res = await axios.post(
"https://my-flask-project-youtube-dl.onrender.com/api/download",
{ url },
{ headers: { Authorization: `Bearer ${token}` } }
);
//window.location.href = `https://my-flask-project-youtube-dl.onrender.com/api/file/${res.data.file}`;
setLoading(false);
downloadFile(res.data.file);
playVideo(res.data.file);
} catch (err) {
setMessage(err.response?.data?.error || "Download failed");
setLoading(false);
setTimeout(()=>{
setMessage("");
},3500);
}
};

if (!token) {
return ( <div className="min-h-screen flex items-center justify-center bg-gray-100"> <div className="bg-white p-8 rounded shadow-md w-96">
{page === "login" ? ( <div> <h2 className="text-2xl font-bold mb-4">Login</h2>
<input
className="w-full p-2 border rounded mb-2"
placeholder="Username"
value={username}
onChange={(e) => setUsername(e.target.value)}
/>
<input
type="password"
className="w-full p-2 border rounded mb-4"
placeholder="Password"
value={password}
onChange={(e) => setPassword(e.target.value)}
/> <button
             className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600"
             onClick={login}
           >
Login </button> <p className="mt-4 text-sm text-gray-600">
Don't have an account?{' '}
<span
className="text-blue-500 cursor-pointer"
onClick={() => setPage("register")}
>
Register here </span> </p> </div>
) : ( <div> <h2 className="text-2xl font-bold mb-4">Register</h2>
<input
className="w-full p-2 border rounded mb-2"
placeholder="Username"
value={username}
onChange={(e) => setUsername(e.target.value)}
/>
<input
type="password"
className="w-full p-2 border rounded mb-4"
placeholder="Password"
value={password}
onChange={(e) => setPassword(e.target.value)}
/> <button
             className="w-full bg-green-500 text-white p-2 rounded hover:bg-green-600"
             onClick={register}
           >
Register </button> <p className="mt-4 text-sm text-gray-600">
Already have an account?{' '}
<span
className="text-blue-500 cursor-pointer"
onClick={() => setPage("login")}
>
Login here </span> </p> </div>
)}
{message && <p className="mt-2 text-red-500">{message}</p>} </div> </div>
);
}

return ( <div className="min-h-screen flex flex-col items-center justify-center bg-gray-100"> <div className="bg-white p-8 rounded shadow-md w-96"> <p className="mb-4">Logged in as <strong>{username}</strong> (Role: {userRole})</p> <button
       className="mb-4 w-full bg-red-500 text-white p-2 rounded hover:bg-red-600"
       onClick={logout}
     >
Logout </button>
{userRole === "admin" && ( <div> 
<textarea
  className="w-full h-40 p-2 border rounded"
  placeholder="Paste cookies.txt content here"
  value={cookies}
  onChange={(e) => setCookies(e.target.value)}
/>
<button
           className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600"
           onClick={uploadCookies}
         >
Upload Cookies </button> 
<h3 className="text-xl font-bold mb-2">Download Video (Admin Only)</h3>
<input
className="w-full p-2 border rounded mb-2"
placeholder="YouTube URL"
value={url}
onChange={(e) => setUrl(e.target.value)}
/> 
<div className="flex flex-row items-start justify-start">
<button
           className={`w-full text-white p-2 rounded ${loading ? "bg-gray-400 cursor-not-allowed" : "bg-blue-500 hover:bg-blue-600"} `}
           onClick={handleDownload}
	disabled={loading}
         >
{loading?'Downloading...':'Download'} </button>
<button
           className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600 ml-2"
           onClick={getStreamUrl}
         >
Stream Video </button> 
</div>
{videoSrc && (
  <video
    src={videoSrc}
    controls
    className="w-full rounded-lg mt-4"
  />
)}
{streamUrl && (
  <video 
    //ref={videoRef} 
    src={`https://my-flask-project-youtube-dl.onrender.com/api/stream-proxy?url=${encodeURIComponent(streamUrl)}`}
    controls
    className="w-full rounded-lg mt-4"
    crossOrigin="anonymous"
  />
)}

<input
  className="mt-4"
  type="file"
  accept="image/*,.pdf"
  onChange={(e) => setFile(e.target.files[0])}
/>

<button className={`w-full text-white p-2 rounded ${loading ? "bg-gray-400 cursor-not-allowed" : "bg-blue-500 hover:bg-blue-600"} `} onClick={uploadOCR} disabled={loading}>
  {loading?'Loading file for Parse OCR...':'Parse OCR'}
</button>

<textarea
  value={ocrText}
  readOnly
  className="w-full h-64"
/>

 </div>
)}
{message && <p className="mt-2 text-red-500">{message}</p>} </div> </div>
);
}

export default App;
